import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { UserService } from '../user.service';

@Component({
  selector: 'app-welcome',
  templateUrl: './welcome.component.html',
  styleUrls: ['./welcome.component.css']
})
export class WelcomeComponent implements OnInit {

  @ViewChild('name') nameRef: ElementRef;

  formValid: boolean = false;
  showName: boolean = false;
  myName: string;
  hollyNumber: number = 0;

  constructor(private userService: UserService) { }

  ngOnInit() { }

  checkValidation() {
    this.formValid = this.nameRef.nativeElement.value.length > 1 ? true : false
  }

  click(){
    this.myName = this.nameRef.nativeElement.value
    this.userService.userName = this.myName
    this.calculateLetters()
  }
  
  isVisible() {
    if (!this.showName) return true
    else return false
  }
  
  calculateLetters() {
    let tempNumber = 0;
    for(let letter of this.myName) {
      tempNumber+=letter.charCodeAt(0)
    }
    this.hollyNumber = this.digitalRoot(tempNumber)
    console.log(this.hollyNumber)
  }

  digitalRoot(number) {
    let strin = number.toString()
    let len = strin.length
    if (len == 1) return number
    let sum = 0;
    for (let i = 0; i < len; i++) {
      sum += parseInt(strin[i])
    }
    return this.digitalRoot(sum)
  }

}
